export const CommonBlocks = (item) => /*html*/ `
    <style>
        .common_blocks_container {
            margin-top: 0;
            margin-bottom: 2px;
            display: flex;
            justify-content: space-between;
            align-items: center;
            gap: 10px;
        }
        .common_blocks_container span:nth-child(1) {
            max-width: 250px;
        }
        .common_blocks_dynamic_value {
            flex: 1;
            font-style: italic;
            background-color: #c2c2c2;
            text-align: right;
        }
    </style>
    <div class="common_blocks_container" id="container-${item.id}">
        <span>${item.label} :</span>
        <span
            class="common_blocks_dynamic_value"
            id=${`d-${item.id}`}
        >
            values to be populated dynamically
        </span>
    </div>
`;
